
#wx-nav nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 50px;
  padding-top: 8px;
  overflow: hidden;
  background: #f9f9f9;
  font-size: 12px;
}
#wx-nav nav dl {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 1;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
}
#wx-nav nav dl.router-link-active dd,
#wx-nav nav dl.router-link-active dt {
  color: #0bb908;
}
#wx-nav nav dl dt {
  position: relative;
  width: 28px;
  height: 28px;
  margin: 0 auto;
  margin-bottom: 2px;
  color: #797979;
  font-size: 28px;
}
#wx-nav nav dl dd {
  color: #929292;
  -webkit-transform-origin: 50% 0;
  transform-origin: 50% 0;
  -webkit-transform: scale(0.9);
  color: #929292;
}


