
.chat-dialogue-entry-collect {
    background-color: #fff;
    position: relative;
    padding: 15px 10px 5px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    width: 100%;
    color: #464646;
    font-size: 14px;
}
.chat-dialogue-entry-collect:before {
    content: "";
    position: absolute;
    width: 200%;
    left: 0;
    bottom: 0;
    transform: scale(.5);
    transform-origin: 0 0;
    -webkit-transform: scale(.5);
    -webkit-transform-origin: 0 0;
    background-color: #b7b7b7;
    height: 1px;
    z-index: 2;
}
.chat-dialogue-entry-collect li {
    float: left;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    -ms-flex-preferred-size: 25%;
        flex-basis: 25%;
    max-width: 25%;
    padding: 5px 10px;
    text-align: center;
}
.chat-dialogue-entry-collect li>div {
    position: relative;
    border-radius: 6px;
    overflow: hidden;
    width: 55px;
    height: 55px;
    margin: 0 auto;
    background-size: cover;
    border: 1px solid #eee;
}
.chat-dialogue-entry-collect li>div img {
    width: 100%;
}
.chat-dialogue-entry-collect li p {
    margin-top: 5px;
}
.chat-dialogue-entry-collect li .iconfont {
    font-size: 23px;
    color: #bbb;
    line-height: 55px
}





