
#wx-header {
  position: relative;
  z-index: 99;
  height: 45px;
  padding: 0 15px 0 10px;
  line-height: 45px;
  backgound: #1b1b1b;
  opacity: 1;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
  transition: all 0.3s liner;
}
#wx-header .center {
  margin: 0 auto;
  text-align: center;
  font-size: 17px;
}
#wx-header .center span {
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Noto Sans CJK SC, WenQuanYi Micro Hei, Arial, sans-serif;
}
#wx-header .center .icon-return-arrow {
  position: absolute;
  left: 10px;
  font-size: 16px;
}
#wx-header .other {
  position: absolute;
  right: 10px;
  cursor: pointer;
}
#wx-header .other > span {
  font-size: 16px;
  display: inline-block;
}
#wx-header .other .icon-tips-jia {
  margin-right: 8px;
  font-size: 16px;
}
#wx-header .other .tips-masker {
  position: fixed;
  left: 0;
  top: 45px;
  width: 100%;
  z-index: 1;
}
#wx-header .other .tips-menu {
  position: absolute;
  right: 0;
  top: 54px;
  width: 155px;
  z-index: 2;
  padding: 0 15px;
  font-size: 14px;
  text-align: left;
  border-radius: 2px;
  background-color: #49484b;
  -webkit-transform-origin: 90% 0;
  transform-origin: 90% 0;
}
.tips-open {
  transition: initial;
  opacity: 1;
}
.tips-close {
  opacity: 0;
  transition: 0.2s opacity ease, 0.6s -webkit-transform ease;
  transition: 0.2s opacity ease, 0.6s transform ease;
  transition: 0.2s opacity ease, 0.6s transform ease, 0.6s -webkit-transform ease;
  -webkit-transform: scale(0);
  transform: scale(0);
}
.other .tips-menu li {
  position: relative;
  height: 40px;
  line-height: 40px;
}
.other .tips-menu li .iconfont {
  margin-right: 15px;
}
.other .tips-menu li:not(:last-child)::after {
  content: "";
  width: 200%;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  background-color: #5b5b5d;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: 0 100%;
  transform-origin: 0 100%;
}
.other .tips-menu::before {
  width: 0;
  height: 0;
  position: absolute;
  top: -7px;
  right: 10px;
  content: "";
  border-width: 0 6px 8px;
  border-color: rgba(0,0,0,0) rgba(0,0,0,0) #49484b rgba(0,0,0,0);
  border-style: solid;
}
.other .tips-menu .iconfont {
  float: left;
  font-size: 16px;
  margin-right: 15px;
}


